import React from "react";
import * as Yup from "yup";
import {
  Box,
  Grid,
  Card,
  Stack,
  Switch,
  TextField,
  FormControlLabel,
  Typography,
  FormHelperText,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
import { useCallback } from "react";
import { Form, FormikProvider, useFormik } from "formik";
import useIsMountedRef from "../../../hooks/userIsMountedRef";
import UploadAvatar from "../../upload/UploadAvatar";
// utils
import { fData } from "../../../utils/fotmatNumbers";
//
import countries from "../countries";
import UploadMultiFile from "../../upload/UploadMultiFile";

function AccountUpdate(props) {
  const isMountedRef = useIsMountedRef();
  const { enqueueSnackbar } = useSnackbar();
  const UpdateUserSchema = Yup.object().shape({
    firstName: Yup.string().required("Name is required"),
  });

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      phoneNumber: "",
      bloodGroupe: "",
      address: "",
      country: "",
      state: "",
      photoURL: "",
      city: "",
      zipCode: "",
      about: "",
      isPublic: false,
      cover: [],
    },

    validationSchema: UpdateUserSchema,
    onSubmit: async (values, { setErrors, setSubmitting }) => {
      try {
        // await updateProfile({ ...values });
        enqueueSnackbar("Update success", { variant: "success" });
        if (isMountedRef.current) {
          setSubmitting(false);
        }
      } catch (error) {
        if (isMountedRef.current) {
          setErrors({ afterSubmit: error.code });
          setSubmitting(false);
        }
      }
    },
  });

  const {
    values,
    errors,
    touched,
    isSubmitting,
    handleSubmit,
    getFieldProps,
    setFieldValue,
  } = formik;

  const handleDrop = useCallback(
    (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (file) {
        setFieldValue("photoURL", {
          ...file,
          preview: URL.createObjectURL(file),
        });
      }
    },
    [setFieldValue]
  );

  const handleDropFiles = useCallback(
    (acceptedFiles) => {
      setFieldValue(
        "cover",
        acceptedFiles.map((file) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );
    },
    [setFieldValue]
  );

  const handleRemoveAll = () => {
    setFieldValue("cover", []);
  };

  const handleRemove = (file) => {
    const filteredItems = values.cover.filter((_file) => _file !== file);
    setFieldValue("cover", [...filteredItems]);
  };
  return (
    <div>
      <Box mb={3}>
        <UploadMultiFile
          files={values.cover}
          onDrop={handleDropFiles}
          onRemove={handleRemove}
          onRemoveAll={handleRemoveAll}
        />
      </Box>

      <FormikProvider value={formik}>
        <Form autoComplete="off" noValidate onSubmit={handleSubmit}>
          <Grid container spacing={3}>
            <Grid item xs={12} md={4}>
              <Card sx={{ py: 10, px: 3, textAlign: "center" }}>
                <UploadAvatar
                  accept="image/*"
                  file={values.photoURL}
                  maxSize={3145728}
                  onDrop={handleDrop}
                  error={Boolean(touched.photoURL && errors.photoURL)}
                  caption={
                    <Typography
                      variant="caption"
                      sx={{
                        mt: 2,
                        mx: "auto",
                        display: "block",
                        textAlign: "center",
                        color: "text.secondary",
                      }}
                    >
                      Allowed *.jpeg, *.jpg, *.png, *.gif
                      <br /> max size of {fData(3145728)}
                    </Typography>
                  }
                />

                <FormHelperText error sx={{ px: 2, textAlign: "center" }}>
                  {touched.photoURL && errors.photoURL}
                </FormHelperText>

                <FormControlLabel
                  control={
                    <Switch {...getFieldProps("isPublic")} color="primary" />
                  }
                  labelPlacement="start"
                  label="Public Profile"
                  sx={{ mt: 5 }}
                />
              </Card>
            </Grid>

            <Grid item xs={12} md={8}>
              <Stack>
                <Card sx={{ p: 3 }}>
                  <Stack spacing={{ xs: 2, md: 3 }}>
                    <Stack direction={{ xs: "column", md: "row" }} spacing={2}>
                      <TextField
                        fullWidth
                        label="First Name"
                        {...getFieldProps("firstName")}
                      />
                      <TextField
                        fullWidth
                        label="Last Name"
                        {...getFieldProps("lastName")}
                      />
                    </Stack>

                    <Stack direction={{ xs: "column", md: "row" }} spacing={2}>
                      <TextField
                        fullWidth
                        label="Email"
                        {...getFieldProps("email")}
                      />
                      <TextField
                        fullWidth
                        label="Mobile"
                        {...getFieldProps("phoneNumber")}
                      />
                    </Stack>
                    <Stack>
                      <TextField
                        fullWidth
                        label="Address"
                        {...getFieldProps("address")}
                      />
                    </Stack>
                    <Stack direction={{ xs: "column", md: "row" }} spacing={2}>
                      <TextField
                        select
                        fullWidth
                        label="Country"
                        placeholder="Country"
                        {...getFieldProps("country")}
                        SelectProps={{ native: true }}
                        error={Boolean(touched.country && errors.country)}
                        helperText={touched.country && errors.country}
                      >
                        <option value="" />
                        {countries.map((option) => (
                          <option key={option.code} value={option.label}>
                            {option.label}
                          </option>
                        ))}
                      </TextField>
                      <TextField
                        fullWidth
                        label="State/Region"
                        {...getFieldProps("state")}
                      />
                    </Stack>

                    <Stack direction={{ xs: "column", md: "row" }} spacing={2}>
                      <TextField
                        fullWidth
                        label="City"
                        {...getFieldProps("city")}
                      />
                      <TextField
                        fullWidth
                        label="Zip/Code"
                        {...getFieldProps("zipCode")}
                      />
                    </Stack>

                    <TextField
                      {...getFieldProps("about")}
                      fullWidth
                      multiline
                      minRows={4}
                      maxRows={4}
                      label="About"
                    />
                  </Stack>

                  <Box
                    sx={{ mt: 3, display: "flex", justifyContent: "flex-end" }}
                  >
                    <LoadingButton
                      type="submit"
                      variant="contained"
                      loading={isSubmitting}
                    >
                      Save Changes
                    </LoadingButton>
                  </Box>
                </Card>
              </Stack>
            </Grid>
          </Grid>
        </Form>
      </FormikProvider>
    </div>
  );
}

export default AccountUpdate;
